import Vue from 'vue'
import Router from 'vue-router'
// 工单打卡
import udenglu from '@/view/worker/workers/ulogin'
import ucenter from '@/view/worker/workers/ucenter'
import ulist from '@/view/worker/workers/daka'
// 工人工资
import wages from '@/view/worker/Processwage/gongrengongzi/gongzi'
import detail from '@/view/worker/Processwage/gongrengongzi/details'
// 筛选
import select from '@/view/worker/Processwage/gongrengongzi/select'
// 设置
import setting from '@/view/worker/Processwage/gongrengongzi/goset'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'udenglu',
      component: udenglu
      // redirect: 'Home'
    },
    {
      path: '/worker/ulogin',
      name: 'udenglu',
      component: udenglu
    },
    {
      path: '/worker/ucenter',
      name: 'ucenter',
      component: ucenter
    },
    {
      path: '/worker/ulist',
      name: 'ulist',
      component: ulist
    },
    // 工人工资
    {
      path: '/worker/wages',
      name: 'wages',
      component: wages
    },
    {
      path: '/worker/workerdetails',
      name: 'detail',
      component: detail
    },
    // 筛选
    {
      path: '/worker/query',
      name: 'select',
      component: select
    },
    // 设置
    {
      path: '/worker/set',
      name: 'setting',
      component: setting
    },

  ]
})



